    AutomaticWatering: boolean;

    //For Rental
    RentStart: Date;
    RentEnd: Date;
    RentDamageDeposit: boolean;
    
    //Distances from key pos
    DistanceFromVillage: number;
    DistanceFromCity: number;
    DistanceFromSea: number;
    DistanceFromAirport: number;
    NearMetro: boolean;

    //Location
    LandZone: LandZone; //

    //Land//
    BuiltSurfaceRatio: number; //   
    BuildingDensityFactor: number; // 
    BuildingFacade: number; // 
    Inclination: number;
    UrbanPlanningZone: boolean = false; //  
    RuralDevelopment: boolean = false;  //   
    Antiparoxi: boolean = false;

